.sidebar{
    display:flex;
    flex-direction:column;
    flex:0.35;
    overflow: scroll;
}
.sidebar__search{
    display:flex;
    align-items: center;
    background-color: #f6f6f6;;
    padding:10px;
    height:30px;
}
.sidebar__searchContainer{
    display:flex;
    align-items:center;
    background-color: white;
    width:100%;
    height:35px;
    border-radius:20px;
}
.sidebar__searchContainer>.MuiSvgIcon-root{
    color:gray;
    padding:10px
}
.sidebar__searchContainer>input{
    border:none;
    margin-left: 10px;
}
.sidebar__header{
    display:flex;
    justify-content:space-between;
    padding:20px;
    border-right:1px solid lightgray
}
.sidebar__headerRight{
    display:flex;
    align-items: center;
    justify-content: space-between;
    min-width:10vw;
}
.sidebar__headerRight>.MuisvgIcon-root{
    margin-right:2vw;
    font-size:24px !important
}
.sidebar__chats{
    background-color:white;
}
a{
    text-decoration:none !important;
    color:black
}
